.input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  width: 100%;
}

.input label {
  margin-right: 5px;
  width: 20%;
}

.input input {
  border: 1px solid black;
  display: block;
  width: 70%;
  border-radius: 5px;
  padding: 5px;
}
